import React from "react";
import styled from "styled-components";
import { useExport } from "../../../../Context/Export";

const TableData = ({ componentRef }) => {
  const exportData = useExport();

  return (
    <Paper ref={componentRef}>
      <ImageBox>
        <Img src={require("../../../images/headerImage.png")} alt="BOU-SST" />
      </ImageBox>
      <Information />
      <Table width="100%">
        <THead>
          {exportData.classInfo && (
            <TableHead courseType={exportData.classInfo.courseType} />
          )}
        </THead>
        <TBody>
          {exportData.student.length > 0 &&
            exportData.student.map((data) => {
              return (
                <TableBodyRows
                  key={data.id}
                  data={data}
                  courseType={exportData.classInfo.courseType}
                />
              );
            })}
        </TBody>
      </Table>
    </Paper>
  );
};

function TableBodyRows({ data, courseType }) {
  return (
    <TRow key={data.id}>
      <TData>{data.stuId}</TData>
      <TData style={{ whiteSpace: "nowrap" }}>{data.stuName}</TData>
      <TData style={{ textAlign: "center" }}>{data.attendance}</TData>
      {courseType === "Lab" ? <TData>{data.labPerform}</TData> : null}
      <TData>{data.assign1}</TData>
      <TData>{data.assign2}</TData>
      <TData>{data.assign3}</TData>
      <TData>{data.average}</TData>
      <TData>{data.test1}</TData>
      <TData>{data.test2}</TData>
      <TData>{data.best}</TData>
      <TData>{data.total}</TData>
    </TRow>
  );
}

const TableHead = ({ courseType }) => {
  return (
    <React.Fragment>
      <TRow>
        <TH rowSpan="2">Student ID</TH>
        <TH rowSpan="2">Student Name</TH>
        <TH rowSpan="2" style={{ width: "100px" }}>
          Class Attendance ({courseType === "Lab" ? 10 : 5})
        </TH>
        {/**
         * Lab Performance
         */}
        {courseType === "Lab" ? (
          <TH rowSpan="2" style={{ width: "70px" }}>
            Lab Performance(10)
          </TH>
        ) : null}
        <TH colSpan="4">Assignment/Case Study (10)</TH>
        <TH colSpan="3">
          Quizzes/Class Test ({courseType === "Lab" ? 10 : 15})
        </TH>
        {/**
         * Total CA Marks
         */}
        <TH rowSpan="2">Total ({courseType === "Lab" ? 40 : 30})</TH>
      </TRow>
      <TRow style={{ background: "rgba(241, 242, 246,1.0)" }}>
        <TH>Assign-1</TH>
        <TH>Assign-2</TH>
        <TH>Assign-3</TH>
        {/**
         * Average Assignment Marks
         */}
        <TH>Average</TH>
        <TH>Test-1</TH>
        <TH>Test-2</TH>
        {/**
         * Best of Quizz/Test Marks
         */}
        <TH>Best</TH>
      </TRow>
    </React.Fragment>
  );
};

export default TableData;

const Paper = styled.div`
  width: 11.69in;
  background: #fff;
  border-radius: 3px;
  padding: 0.3in;
`;

const ImageBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`;

const Table = styled.table`
  font-size: 0.9rem;
  font-weight: 500;
  padding: 1px;
  border-radius: 5px;
  box-sizing: border-box;
  border-color: gray;
`;

const THead = styled.thead`
  background: rgba(241, 242, 246, 1);
`;

const TBody = styled.tbody`
  font-weight: 400;
`;

const TRow = styled.tr`
  &:nth-child(2n + 2) {
    background: rgba(240, 240, 240, 0.3);
  }
`;

const TData = styled.td`
  border: 1px solid rgba(3, 3, 3, 0.3);
  padding: 0.3rem 0.5rem;
  &:nth-child(n + 4) {
    text-align: center;
  }
`;

const TH = styled.th`
  border: 1px solid rgba(3, 3, 3, 0.4);
  padding: 0.3rem 0.5rem;
  font-size: 0.8rem;
  text-align: center;
  font-weight: 700;
  min-width: 75px;
`;

const Img = styled.img`
  width: 100;
  height: auto;
  border-radius: 5px;
`;

const InformationPanel = styled.div`
  width: 100%;
`;

const Title = styled.h1`
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
`;

const Panel = styled.div`
  width: 100%;
  margin-top: 1.3rem;
  overflow: hidden;
`;

const SubPanel = styled.div`
  display: grid;
  gap: 0.5rem;

  margin-bottom: 1rem;
  &:nth-child(1) {
    grid-template-columns: 45% 30% 25%;
  }

  &:nth-child(2) {
    grid-template-columns: 45% 55%;
  }
`;
const GroupText = styled.div`
  display: flex;
  flex-wrap: nowrap;
`;
const Label = styled.p`
  margin-bottom: 0.3rem;
  font-weight: 500;
  font-size: 1rem;
  width: 290px;
`;
const Text1 = styled.p`
  margin-bottom: 0.3rem;
  font-weight: 600;
  font-size: 1rem;
  border-bottom: 1px solid #333;
  width: 100%;
`;
const Span = styled.span`
  width: 10px;
  margin-right: 10px;
  font-weight: 700;
`;

const Information = () => {
  const exportData = useExport();
  const classInfo = exportData.classInfo;
  return (
    <InformationPanel>
      <Title>B. Sc in Computer Science and Engineering Program</Title>
      <Title>
        <span style={{ fontWeight: "600" }}>Study Center :</span>{" "}
        {classInfo.study_center}
      </Title>
      <Panel>
        <SubPanel>
          <GroupText>
            <Label>Year and Semester</Label>
            <Span>:</Span>
            <Text1>{getSemester(classInfo.year, classInfo.semester)}</Text1>
          </GroupText>
          <GroupText>
            <Label style={{ width: "170px", marginLeft: "20px" }}>
              Session
            </Label>
            <Span>:</Span>
            <Text1>{classInfo.session}</Text1>
          </GroupText>
          <GroupText>
            <Label style={{ width: "170px", marginLeft: "20px" }}>Term</Label>
            <Span>:</Span>
            <Text1>{classInfo.term}</Text1>
          </GroupText>
        </SubPanel>
        <SubPanel>
          <GroupText>
            <Label>Course Teacher</Label>
            <Span>:</Span>
            <Text1>{classInfo.teacherName}</Text1>
          </GroupText>
          <GroupText>
            <Label style={{ width: "170px", marginLeft: "20px" }}>
              Course Name
            </Label>
            <Span>:</Span>
            <Text1>{classInfo.courseName}</Text1>
          </GroupText>
        </SubPanel>
        <SubPanel></SubPanel>
      </Panel>
    </InformationPanel>
  );
};

const getSemester = (a, b) => {
  if (a === 1) {
    return b === 1 ? "First Year First Semester" : "First Year Second Semester";
  } else if (a === 2) {
    return b === 1
      ? "Second Year First Semester"
      : "Second Year Second Semester";
  } else if (a === 3) {
    return b === 1 ? "Third Year First Semester" : "Third Year Second Semester";
  } else {
    return b === 1
      ? "Fourth Year First Semester"
      : "Fourth Year Second Semester";
  }
};
