import React from "react";
import styled from "styled-components";
import { useMarksEntry } from "../../Context/MarksEntry";
import { useExport } from "../../Context/Export";
import useAxios from "../../api/Axios";
import ExportData from "./Export/ExportData";
import { handleError, handleSuccess } from "../../utils/Toastify";
import AlertDialog from "./DialogBox/AlertDialog";
import { useAddStudent } from "../../Context/AddStudent";

const ToolsBox = ({ classInfo, onReloadPage }) => {
  const axios = useAxios();
  const marks = useMarksEntry();
  const students = useAddStudent();
  const exportData = useExport();
  const [loading, setLoading] = React.useState(true);
  const [showDialog, setShowDialog] = React.useState(false);
  const [dataLength, setDataLength] = React.useState(0);
  React.useEffect(() => {
    setLoading(true);
    exportData.handleSetClassInfo(classInfo);
    marks.handleSetClassInfo(classInfo);
    const getData = async () => {
      const url = "/student/all?id=" + classInfo.classId;
      await axios.get(url).then((res) => {
        setDataLength(res.data.length);
        exportData.handleSetStudent(res.data);
      });
    };
    if (dataLength === 0) {
      getData();
    }
    setLoading(false);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [axios, classInfo]);
  const handleDeleteTable = async () => {
    try {
      const url = "/class/delete/table?id=" + classInfo.classId;
      await axios
        .delete(url)
        .then((res) => {
          handleSuccess(res.data);
          setShowDialog(false);
          onReloadPage();
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    }
  };

  return (
    <React.Fragment>
      {loading ? (
        "Loading..."
      ) : (
        <Tools>
          <div>
            <Button
              disabled={dataLength === 0}
              onClick={() => exportData.handleOpen(true)}
            >
              Export
            </Button>
            <Button
              disabled={dataLength === 0}
              onClick={() => setShowDialog(true)}
            >
              Delete Table
            </Button>
            <Button onClick={() => marks.handleOpenBox(true)}>Add Marks</Button>
            <Button onClick={() => students.handleOpen(true)}>
              Add Student
            </Button>
            <ExportData />
          </div>
          <div>
            <Input
              disabled={dataLength === 0}
              type="search"
              placeholder="Search"
              defaultValue={marks.search}
              onChange={marks.handleSearch}
            />
          </div>
        </Tools>
      )}
      {showDialog && (
        <AlertDialog
          title="Delete Table"
          open={showDialog}
          setOpen={setShowDialog}
          message="Are you sure you want to delete this table?"
          onConfirm={() => {
            handleDeleteTable();
          }}
        />
      )}
    </React.Fragment>
  );
};

export default ToolsBox;

const Tools = styled.div`
  display: grid;
  grid-template-columns: auto 230px;
  margin: 0.5rem 0;
  padding: 0.3rem 0rem;
  width: 100%;
  color: #fff;
  flex-wrap: wrap;
  @media (max-width: 860px) {
    grid-template-columns: auto;
  }
`;

const Input = styled.input`
  padding: 0.3rem 1rem;
  border-radius: 5px;
  border: 1px solid grba(3, 3, 3, 0.5);
  margin: 0.4rem 0;

  &:hover {
    ${(props) =>
      props.disabled
        ? `
      &::before {
        content: "Please Import Student";
        height: 35px;
        white-space: nowrap;
        display: flex;
        align-items: center;
        font-size: 0.5 rem;
        position: absolute;
        left: 105%;
        top: 0;
        bottom: 0;
        margin: auto;
        background: #fff;
        color: #333;
        z-index: 1;
        padding: 0.2rem 0.3rem;
        border-radius: 5px;
        box-shadow: 1px 1px 5px 5px rgba(3,3,3,0.2);
      }
      &::after {
        content: "";
        position: absolute;
        left: 93%;
        top: 0;
        bottom: 0;
        margin: auto;
        height: 10px;
        width: 10px;
        color: #333;
        z-index: 1;
        border-style: solid;
        border-width: 10px 10px 10px 10px;
        border-color: transparent #fff transparent transparent;
      }
    `
        : ""}
  }
`;

const Button = styled.button`
  min-width: 100px;
  height: 40px;
  font-weight: 500;
  background: green;
  margin: 0.3rem;
  color: #fff;
  border-radius: 5px;
  position: relative;
  &:nth-child(1) {
    margin-left: 0;
  }
  &:hover {
    ${(props) =>
      props.disabled
        ? `
      &::before {
        content: "Please Import Student";
        height: 35px;
        white-space: nowrap;
        display: flex;
        align-items: center;
        font-size: 0.5 rem;
        position: absolute;
        left: 105%;
        top: 0;
        bottom: 0;
        margin: auto;
        background: #fff;
        color: #333;
        z-index: 1;
        padding: 0.2rem 0.3rem;
        border-radius: 5px;
        box-shadow: 1px 1px 5px 5px rgba(3,3,3,0.2);
      }
      &::after {
        content: "";
        height: 10px;
        width: 10px;
        position: absolute;
        left: 93%;
        top: 0;
        bottom: 0;
        margin: auto;
        color: #333;
        z-index: 1;
        border-style: solid;
        border-width: 10px 10px 10px 10px;
        border-color: transparent #fff transparent transparent;
      }
    `
        : ""}
  }
`;
