import * as React from "react";
import Dialog from "@mui/material/Dialog";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Slide from "@mui/material/Slide";
import { IoClose } from "react-icons/io5";
// import { TiArrowBack } from "react-icons/ti";
import styled from "styled-components";
import { useExport } from "../../../Context/Export";
import TableData from "./Pdf/TableData";
import Button from "@mui/material/Button";
import ReactToPrint from "react-to-print";
import { BsFillPrinterFill } from "react-icons/bs";
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function ExportData() {
  const componentRef = React.useRef();
  const exportData = useExport();

  React.useEffect(() => {}, [exportData.openBox]);

  const handleClose = () => {
    exportData.handleOpenBox(false);
  };

  return (
    <div>
      <Dialog
        fullScreen
        open={exportData.openBox}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        {/**
         * Top Tools Bar
         */}
        <ToolsBar componentRef={componentRef} />
        <MarkContainer>
          <TableData
            classInfo={exportData.classInfo}
            student={exportData.student}
            componentRef={componentRef}
          />
        </MarkContainer>
      </Dialog>
    </div>
  );
}

function ToolsBar({ componentRef }) {
  const exportData = useExport();
  return (
    <AppBar
      sx={{
        position: "relative",
      }}
    >
      <Toolbar>
        <IconButton
          color="inherit"
          onClick={() => exportData.handleOpen(false)}
          aria-label="close"
        >
          <IoClose />
        </IconButton>
        <Heading>Export Data</Heading>
        {/* <Button autoFocus color="inherit" onClick={handleClose}>
       save
      </Button> */}
        <p
          className="courseTitle"
          style={{
            marginBottom: "0",
            marginRight: "2rem",
            textAlign: "center",
            width: "80%",
          }}
        >
          {exportData.classInfo.courseName +
            " - Term: " +
            exportData.classInfo.term}
        </p>
        <div style={{ marginLeft: "auto" }}>
          <ReactToPrint
            documentTitle={
              exportData.classInfo.courseName +
              " - " +
              exportData.classInfo.term +
              " - " +
              new Date()
            }
            trigger={() => (
              <Button autoFocus color="inherit" onClick={() => {}}>
                <span style={{ marginRight: "5px", fontSize: "1.3rem" }}>
                  <BsFillPrinterFill />
                </span>
                Print
              </Button>
            )}
            content={() => componentRef.current}
          />
        </div>
      </Toolbar>
    </AppBar>
  );
}

const Heading = styled.p`
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 0rem;
`;

const MarkContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  overflow: scroll;
  padding-top: 0.5rem;
  background: rgba(3, 3, 3, 0.6);
  @media (max-width: 768px) {
    display: block !important;
  }
`;
